
.nav-tag {
  padding: 20px;
}
.nav-tag > li {
    display: inline-block;
    margin: 5px;
}
.nav-tag .tag {
    display: inline-block;
    padding: 0 20px;
    border: 1px solid;
    white-space: nowrap;
    border-radius: 16px;
    font-size: 16px;
    line-height: 32px;
}
.nav-tag .tag.router-link-active {
      color: orange;
}
.nav-tag .tag:hover {
      text-decoration: underline;
}



