
.vue-carousel {
  height: 100%;
  overflow: hidden;
  position: relative;
}
.vue-carousel:hover .prev,
  .vue-carousel:hover .next {
    display: block;
}
.imgs-container {
  height: 100%;
}
.imgs-container .img-item {
    display: inline-block;
    width: 100%;
    height: 100%;
    background: #ccc no-repeat center/cover;
}
.prev,
.next {
  display: none;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 44px;
  font-size: 60px;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  font-weight: 200;
  line-height: 80px;
}
.prev {
  left: 4%;
}
.next {
  right: 4%;
}
.dots {
  display: table;
  margin: 0 auto;
  -webkit-transform: translateY(-120%);
          transform: translateY(-120%);
}
.dots .dot {
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 12px;
    height: 12px;
    margin: 6px;
    border: 2px solid transparent;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.7);
    cursor: pointer;
}
.dots .dot.active-dot {
      border: 2px solid #f84e4e;
}



